import React from "react";
import styled from "styled-components";

import { PageHeader, Descriptions } from "antd";

const Wrapper = styled.div`
  width: 100%;
  height: 15vh;
  background-color: #aed581;
  display: flex;
  justify-content: center;
  align-items: center;
`;

const Container = styled.div`
  width: ${(props) => (props.homePage ? "75vw" : "85vw")};
  display: flex;
  justify-content: space-between;
  align-items: center;
`;

const PageHeaderWrapper = styled(PageHeader)`
  display: ${(props) => props.homePage && "flex"};
  background: #aed581;
  height: 15vh;
  padding: 0;
`;

const Header = ({ category, numberOfMillitarys, data }) => {
  const homePage = category === "Головна";
  return (
    <Wrapper>
      <Container homePage={homePage}>
        <div className="site-page-header-ghost-wrapper">
          <PageHeaderWrapper
            ghost={false}
            onBack={() => window.history.back()}
            title={category}
            homePage={homePage}
          >
            {!homePage && (
              <Descriptions
                size="default"
                column={4}
                labelStyle={{ fontSize: "16px" }}
                contentStyle={{ fontSize: "16px" }}
              >
                <Descriptions.Item label="Кількість військовослужбовців">
                  <strong>{numberOfMillitarys}</strong>
                </Descriptions.Item>
                <Descriptions.Item label="">
                </Descriptions.Item>
                <Descriptions.Item label=""></Descriptions.Item>
                <Descriptions.Item label=""></Descriptions.Item>
                <Descriptions.Item label="За 2021 рік перебували на лікуванні"></Descriptions.Item>
                <Descriptions.Item label="Вдома">
                  <strong>{data.home}</strong>
                </Descriptions.Item>
                <Descriptions.Item label="Шпиталь">
                  <strong>{data.hospital}</strong>
                </Descriptions.Item>
                <Descriptions.Item label="Медичний пункт">
                  <strong>{data.medPoint}</strong>
                </Descriptions.Item>
              </Descriptions>
            )}
          </PageHeaderWrapper>
        </div>
      </Container>
    </Wrapper>
  );
};

export default Header;
