import React from "react";

import { Form, Input, Select, DatePicker, Modal } from "antd";

const AddMedicalHistoryModal = ({ visible, onCancel, onSend }) => {
  const [form] = Form.useForm();
  const handleOnSend = () => {
    form
      .validateFields()
      .then((values) => {
        form.resetFields();
        onSend(values);
      })
      .catch((info) => {
        console.log("Validate Failed", info);
      });
  };
  const handleOnCancel = () => {
    form.resetFields();
    onCancel();
  };

  return (
    <Modal
      title="Додати дані в історію хвороби"
      visible={visible}
      onOk={handleOnSend}
      onCancel={handleOnCancel}
    >
      <Form
        form={form}
        name="add-ill"
        labelCol={{ span: 8 }}
        wrapperCol={{ span: 16 }}
      >
        <Form.Item
          name="onsetOfDeases"
          label="Початок хвороби"
          rules={[{ required: true, message: `Будь ласка, заповніть поле` }]}
        >
          <DatePicker />
        </Form.Item>
        <Form.Item
          name="nameOfDiagnosis"
          label="Діагноз"
          rules={[{ required: true, message: `Будь ласка, заповніть поле` }]}
        >
          <Input />
        </Form.Item>
        <Form.Item
          name="placeOfTreatment"
          label="Місце лікування"
          rules={[{ required: true, message: `Будь ласка, заповніть поле` }]}
        >
          <Select>
            <Select.Option value="Вдома">Вдома</Select.Option>
            <Select.Option value="Шпиталь">Шпиталь</Select.Option>
            <Select.Option value="Медичний пункт">Медичний пункт</Select.Option>
          </Select>
        </Form.Item>
        <Form.Item
          name="dayOfDischarge"
          label="Кінець хвороби"
          rules={[{ required: true, message: `Будь ласка, заповніть поле` }]}
        >
          <DatePicker />
        </Form.Item>
      </Form>
    </Modal>
  );
};

export default AddMedicalHistoryModal;
