import React, { useEffect, useState } from "react";

import { useSelector } from "react-redux";
import { BrowserRouter, Routes, Route } from "react-router-dom";

import List from "./components/List/List";
import HomePage from "./components/Home/HomePage";

const App = () => {
  const { millitarys } = useSelector((state) => state);
  const { currentYear } = millitarys;

  const [state, setState] = useState(millitarys);

  useEffect(() => {
    const data = localStorage.getItem(currentYear);
    if (data) {
      setState(JSON.parse(data));
    }
  }, [millitarys, currentYear]);

  return (
    <BrowserRouter>
      <Routes>
        <Route path="/" element={<HomePage />} />
        <Route
          path="officers"
          element={
            <List
              millitarys={state.officers}
              type="officers"
              header="Офіцери"
            />
          }
        />
        <Route
          path="sergeants"
          element={
            <List
              millitarys={state.sergeants}
              type="sergeants"
              header="Контрактники"
            />
          }
        />
        <Route
          path="soldiers"
          element={
            <List
              millitarys={state.soldiers}
              type="soldiers"
              header="Строкова служба"
            />
          }
        />
      </Routes>
    </BrowserRouter>
  );
};

export default App;
