import React from "react";

import { Modal } from "antd";

const RemoveMillitaryModal = ({ visible, onSend, onCancel }) => {
  return (
    <Modal
      title="Видалити військовослужбовця"
      visible={visible}
      onOk={onSend}
      onCancel={onCancel}
    >
      <p>
        Ви впевнені, що хочете видалити даного військовослужбовця зі списку?
      </p>
    </Modal>
  );
};

export default RemoveMillitaryModal;
