import React from "react";
import {
  officersRanks,
  sergeantsRanks,
  soldiersRanks,
} from "../../../../db/ranks";

import { Input, Select, Form, Modal } from "antd";

const { Option } = Select;

const AddingMillitaryModal = ({
  visible,
  onCancel,
  onSend,
  position,
  type,
}) => {
  const [form] = Form.useForm();

  const addingPosition = position === "top" ? "перед цим" : "після цього";

  const defineTypeOfMillitarys = (type) => {
    if (type === "sergeants") return [...sergeantsRanks];
    if (type === "soldiers") return [...soldiersRanks];
    return [...officersRanks];
  };

  const handleOnSend = () => {
    form
      .validateFields()
      .then((values) => {
        form.resetFields();
        onSend(values, position);
      })
      .catch((info) => {
        console.log("Validate Failed", info);
      });
  };

  const handleOnCanel = () => {
    form.resetFields();
    onCancel();
  };

  const typeOfMillitarys = defineTypeOfMillitarys(type);
  
  return (
    <Modal
      title="Додати військовослужбовця в список"
      visible={visible}
      onOk={handleOnSend}
      onCancel={handleOnCanel}
      okButtonProps={{
        form: "add-millitary",
        key: "submit",
        htmlType: "submit",
      }}
    >
      <p>
        Додати в/с в список <strong>{addingPosition}</strong> в/с
      </p>
      <Form form={form} name="add-millitary">
        <Form.Item
          name="rank"
          rules={[{ required: true, message: `Будь ласка, заповніть поле` }]}
        >
          <Select placeholder={typeOfMillitarys[0]}>
            {typeOfMillitarys.map((item, index) => (
              <Option key={index} value={item}>
                {item}
              </Option>
            ))}
          </Select>
        </Form.Item>
        <Form.Item
          name="name"
          rules={[{ required: true, message: `Будь ласка, заповніть поле` }]}
        >
          <Input placeholder="СЕНИШАК Микола Іванович" />
        </Form.Item>
      </Form>
    </Modal>
  );
};

export default AddingMillitaryModal;
