import React from "react";
import styled from "styled-components";

const BlockOfData = styled.div`
  display: flex;
  justify-content: space-between;
  width: 50%;
`;
const Place = styled.div``;
const Number = styled.div``;

const PlaceOfTreatment = ({ data }) => {
  return (
    <div>
      <h3>Перебував на лікуванні:</h3>
      <BlockOfData>
        <Place>Вдома:</Place>
        <Number>
          <strong>{data.home}</strong>
        </Number>
      </BlockOfData>
      <BlockOfData>
        <Place>Шпиталь:</Place>
        <Number>
          <strong>{data.hospital}</strong>
        </Number>
      </BlockOfData>
      <BlockOfData>
        <Place>Медичний пункт:</Place>
        <Number>
          <strong>{data.medPoint}</strong>
        </Number>
      </BlockOfData>
    </div>
  );
};

export default PlaceOfTreatment;
